/* Global Styles  */
.container-fluid {
  background-color: #ebebeb;
  border-style: solid;
  border-width: 20px;
  border-color: #ba00ff;
}
.jumbotron {
  background-image: url("../src/components/images/nurseandlady.jpeg");
  background-size: 80vw;
  height: 50vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.card {
  background-color: #006aff;
  height: 15vh;
  margin-bottom: 20px;
  border-radius: 10%;
}
.card-text {
  color: white;
  text-align: center;
}
.spacer {
  height: 15vh;
}
